/* eslint-disable react/no-unescaped-entities */


"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Card, CardHeader, CardContent, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Loader2, FileUp } from "lucide-react";
import { format, parseISO } from "date-fns";
import { fr } from "date-fns/locale";

interface Soutenance {
  idSoutenance: number;
  date: string;
  time: string;
  location: string;
  nomGroupe: string;
  status: "Scheduled" | "Completed" | "Pending" | "Cancelled";
}

export default function ProfSoutenancesOverview() {
  const [soutenances, setSoutenances] = useState<Soutenance[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<string>("all");
  const router = useRouter();

  const idEncadrant = 1; // Replace with dynamic logic (e.g., from auth)

  useEffect(() => {
    const fetchSoutenances = async () => {
      try {
        const response = await fetch(`http://localhost:5000/api/soutenances/encadrant/${idEncadrant}`);
        if (!response.ok) throw new Error("Erreur lors du chargement des soutenances");
        const data = await response.json();
        setSoutenances(data);
      } catch (err) {
        setError(err instanceof Error ? err.message : "Une erreur inconnue est survenue");
      } finally {
        setLoading(false);
      }
    };
    fetchSoutenances();
  }, [idEncadrant]);

  const filteredSoutenances = statusFilter === "all" 
    ? soutenances 
    : soutenances.filter(s => s.status === statusFilter);

  if (loading) {
    return (
      <Card className="w-full">
        <CardContent className="p-6 flex justify-center">
          <Loader2 className="h-8 w-8 animate-spin" />
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card className="w-full">
        <CardContent className="p-6 text-center">
          <p className="text-red-500">{error}</p>
          <Button variant="outline" onClick={() => window.location.reload()} className="mt-4">
            Réessayer
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="w-full border-none shadow-lg">
      <CardHeader className="bg-muted/10">
        <div className="flex justify-between items-center">
          <CardTitle className="text-2xl font-semibold">Vue d'ensemble des soutenances</CardTitle>
          <Button 
            onClick={() => router.push("/professor/documents")}
            className="flex items-center gap-2"
          >
            <FileUp className="h-4 w-4" />
            Documents
          </Button>
        </div>
      </CardHeader>
      <CardContent className="p-6">
        <div className="mb-4">
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Filtrer par statut" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Tous</SelectItem>
              <SelectItem value="Scheduled">Planifié</SelectItem>
              <SelectItem value="Completed">Terminé</SelectItem>
              <SelectItem value="Pending">En attente</SelectItem>
              <SelectItem value="Cancelled">Annulé</SelectItem>
            </SelectContent>
          </Select>
        </div>
        {filteredSoutenances.length === 0 ? (
          <p className="text-center text-muted-foreground">Aucune soutenance correspondante.</p>
        ) : (
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Groupe</TableHead>
                <TableHead>Date</TableHead>
                <TableHead>Heure</TableHead>
                <TableHead>Lieu</TableHead>
                <TableHead>Statut</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredSoutenances.map((soutenance) => (
                <TableRow key={soutenance.idSoutenance}>
                  <TableCell>{soutenance.nomGroupe}</TableCell>
                  <TableCell>
                    {format(parseISO(soutenance.date), "d MMMM yyyy", { locale: fr })}
                  </TableCell>
                  <TableCell>{soutenance.time}</TableCell>
                  <TableCell>{soutenance.location}</TableCell>
                  <TableCell>{soutenance.status}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}
      </CardContent>
    </Card>
  );
}